export const typeCheck = (stype, type1, type2) => {
  if (typeChart[stype][type1] === 0 || typeChart[stype][type2] === 0) {
    return 0;
  }
  let typeDamage = 1;
  if (typeChart[stype]) {
    if (typeChart[stype][type1]) {
      typeDamage *= typeChart[stype][type1];
    }
    if (typeChart[stype][type2]) {
      typeDamage *= typeChart[stype][type2];
    }
  }

  return typeDamage;
};

export const typeCheckAbil = (battle, stype, type1, type2) => {
  //     
  // ,          
  if (typeChart[stype][type1] === 0 || typeChart[stype][type2] === 0) {
    if (
      battle[battle.turn.atk].abil === "" &&
      (stype === "" || stype === "") &&
      (type1 === "" || type2 === "")
    ) {
      //  
    } else {
      return 0;
    }
  }
  let typeDamage = 1;
  if (typeChart[stype]) {
    if (typeChart[stype][type1]) {
      //0  (   )
      typeDamage *= typeChart[stype][type1];
    }
    if (typeChart[stype][type2]) {
      typeDamage *= typeChart[stype][type2];
    }
  }

  return typeDamage;
};

export const typeCheckConsole = (stype, type1, type2) => {
  const typeDamage = typeCheck(stype, type1, type2);
  if (typeDamage === 1) {
    return "  ";
  }
  if (typeDamage > 1) {
    return "  ";
  }
  if (typeDamage === 0) {
    return "  ";
  }
  if (typeDamage < 1) {
    return "  ";
  }
};

export const typeCheckText = (stype, type1, type2) => {
  let typeDamage;
  if (!type1 && !type2) {
    typeDamage = stype;
    //   typeDamage   
  } else {
    typeDamage = typeCheck(stype, type1, type2);
  }
  if (typeDamage === 1) {
    return null;
  }
  if (typeDamage > 1) {
    return " !";
  }
  if (typeDamage === 0) {
    return "   ...";
  }
  if (typeDamage < 1) {
    return "   ...";
  }
};

