export const statCalculate = (battle) => {
  const player = battle.player;
  const npc = battle.npc;

  const attributes = ["atk", "def", "catk", "cdef", "speed"];

  attributes.forEach((attr) => {
    player[attr] =
      player.origin[attr] * getMultiplier(player.tempStatus.rank[attr]);
    npc[attr] = npc.origin[attr] * getMultiplier(npc.tempStatus.rank[attr]);
  });

  [player, npc].forEach((entity) => {
    if (entity.item === "") {
      entity.cdef *= 1.5;
    }
    [battle.player, battle.npc].forEach((entity, index, arr) => {
      const opponent = arr[1 - index]; // player npc  

      if (entity.item === "") {
        entity.cdef *= 1.5;
      }

      if (entity.item === "") {
        entity.speed *= 1.5;
      }

      if (entity.item === "") {
        entity.catk *= 1.5;
      }

      if (entity.item === "") {
        entity.atk *= 1.5;
      }

      if (entity.abil === "" && opponent.abil !== "") {
        opponent.def *= 0.75;
      }

      if (
        entity.abil === "" &&
        battle.field.field === ""
      ) {
        entity.catk *= 1.3;
      }
    });
  });
  return battle;
};

export const getMultiplier = (rank) => {
  const mRank = {
    "-1": 2 / 3,
    "-2": 1 / 2,
    "-3": 2 / 5,
    "-4": 1 / 3,
    "-5": 2 / 7,
    "-6": 1 / 4,
  };
  if (rank >= 1 && rank <= 6) {
    return 1 + rank * 0.5; // : 1  1.5, 2  2
  } else if (rank <= -1 && rank >= -6) {
    return mRank[String(rank)];
  } else if (rank === 0) {
    return 1;
  }
  console.error("   ");
  return 1; // rank 0    
};
