import { rank } from "../function/rank";

export const abil = (bt, atks, enqueue) => {
  // 
  // ,   
  // battleScrren.js useEffect
  // switch.js

  let defs;

  if (atks === "player") {
    defs = "npc";
  } else if (atks === "npc") {
    defs = "player";
  }

  const atk = bt[atks];
  const def = bt[defs];
  const atkAbil = atk.abil;
  const defAbil = def.abil;

  if (atkAbil === "") {
    enqueue({
      battle: bt,
      text: "[ ] " + atk.name + "   !",
    });
  }
  //    statCalculate  text 

  if (atkAbil === "") {
    const text = "[ ]";
    rank(bt, enqueue, atks, "atk", 1, text);
  }

  if (atkAbil === "" && bt.field.weather !== "") {
    bt.field.weather = "";
    bt.field.weatherTurnRemain = 5;
    enqueue({
      battle: bt,
      text: "[ ] " + atk.name + "    !",
    });
  }

  if (atkAbil === "" && bt.field.field !== "") {
    bt.field.field = "";
    bt.field.fieldTurnRemain = 5;
    enqueue({
      battle: bt,
      text: "[ ] " + atk.name + "   !",
    });
  }

  if (atkAbil === "" && bt.field.field !== "") {
    bt.field.field = "";
    bt.field.fieldTurnRemain = 5;
    enqueue({
      battle: bt,
      text:
        "[ ] " +
        atk.names +
        "     !",
    });
  }
};
