import React, { useState } from "react";
import TextBox from "../TextBox";
import ChangeButton from "../ChangeButton";
import BenchPokemon from "./BenchPokemon";
import { battleStart } from "../../../service/battleStart";
import { switchPlayer, switchNpc } from "../../../service/switch";
import { abil } from "../../../service/abil";
import { speedCheck } from "../../../util/speedCheck";
import { turnEnd } from "../../../service/turnEnd";
import { attackNpc } from "../../../service/attack";
const BottomSectionSwitch = ({
  battle,
  text,
  bottom,
  setBottom,
  setBench,
  queueObject,
}) => {
  const [selected, setSelected] = useState(null);
  const handleSelected = (bench) => {
    setSelected((prev) => (prev === bench ? null : bench));
  };

  let handleSwitch;
  if (bottom === "mustSwitch") {
    //    
    handleSwitch = (index) => {
      setBottom("skill");
      let bt = structuredClone(battle);
      switchPlayer(bt, index, queueObject.enqueue);
      queueObject.dequeue(); // " ?" dequque   

      if (bt.npc.faint) {
        // npc   turnEnd.js 
        // player npc    
        //       
        // bottomSwitch .
        if (bt.npcBench1.faint !== true) {
          // 1   1 
          switchNpc(bt, "npcBench1", queueObject.enqueue);
        } else if (bt.npcBench2.faint !== true && bt.npcBench1.faint === true) {
          //1  2   2 
          switchNpc(bt, "npcBench2", queueObject.enqueue);
        }
      }
    };
  } else if (bottom === "uturn") {
    //    
    handleSwitch = (index) => {
      setBottom("skill");
      let bt = structuredClone(battle);
      bt.uturn = null;
      switchPlayer(bt, index, queueObject.enqueue);
      queueObject.dequeue(); // " ?" dequque   

      if (
        bt.turn.fastUser === "npc" || // npc    =    
        bt.npc.faint || // npc    
        bt.turn.fastUser === null // npc    =    
      ) {
        turnEnd(bt, queueObject.enqueue);
      } else if (bt.turn.fastUser === "player") {
        //   
        //   npc  
        attackNpc(bt, bt.turn.playerSN, bt.turn.npcSN, queueObject.enqueue);
        turnEnd(bt, queueObject.enqueue);
      }
    };
  } else if (bottom === "switch") {
    //    
    handleSwitch = (index) => {
      setBottom("skill");
      battleStart(battle, index, queueObject);
    };
  }

  return (
    <>
      <TextBox text={text} />

      <BenchPokemon
        battle={battle}
        index={"player"}
        selected={selected}
        handleSelected={handleSelected}
        setBench={setBench}
        setBottom={setBottom}
        handleSwitch={handleSwitch}
      />

      <BenchPokemon
        battle={battle}
        index={"playerBench1"}
        selected={selected}
        handleSelected={handleSelected}
        setBench={setBench}
        setBottom={setBottom}
        handleSwitch={handleSwitch}
      />

      <BenchPokemon
        battle={battle}
        index={"playerBench2"}
        selected={selected}
        handleSelected={handleSelected}
        setBench={setBench}
        setBottom={setBottom}
        handleSwitch={handleSwitch}
      />

      {bottom === "switch" && (
        <ChangeButton
          onClick={() => setBottom("skill")}
          innerText={""}
        />
      )}
    </>
  );
};

export default BottomSectionSwitch;
