export const damage = (
  battle,
  skillDamage,
  getDamagePokemon,
  enqueue,
  text
) => {
  let pokemon;
  if (getDamagePokemon === "npc") {
    pokemon = battle.npc;
  } else if (getDamagePokemon === "player") {
    pokemon = battle.player;
  }
  const skDamage = Math.floor(skillDamage);

  if (pokemon.hp <= 0) {
    return;
  }

  let gdTrigger = false;
  if (pokemon.item === "" && pokemon.hp === pokemon.origin.hp) {
    gdTrigger = true;
  }
  pokemon.hp -= skDamage;
  if (pokemon.hp <= 0) {
    pokemon.hp = 0;
    if (gdTrigger) {
      pokemon.item = null;
      pokemon.hp = 1;
    }
  }
  if (text) {
    enqueue({ battle: battle, text: text });
  }
  if (battle.turn.atk && battle[battle.turn.atk].temp.critical) {
    enqueue({ battle: battle, text: " !" });
  }
  if (gdTrigger && pokemon.item === null) {
    enqueue({ battle: battle, text: pokemon.names + "   !" });
  }
  if (pokemon.hp === 0) {
    pokemon.faint = true;
    Object.keys(pokemon.status).forEach((key) => {
      pokemon.status[key] = null;
    });
    //   
    enqueue({ battle: battle, text: pokemon.names + " !" });
  }
};
